import TelaCadastroCliente from "./components/Telas/TelaCadastroCliente";
import TelaCadastroFornecedor from "./components/Telas/TelaCadastroFornecedor";
import TelaCadastroUsuario from "./components/Telas/TelaCadastroUsuario";
import TelaCadastroProduto from "./components/Telas/TelaCadastroProduto";
import TelaCadastroCategoria from "./components/Telas/TelaCadastroCategoria";
import TelaMenu from "./components/Telas/TelaMenu";
import Tela404 from "./components/Telas/Tela404";
import TelaLogin from "./components/Telas/TelaLogin";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import { useState, createContext, useEffect } from "react";
import { jwtDecode } from "jwt-decode";

export const ContextoUsuario = createContext();

function App() {
    const [processando, setProcessando] = useState(true);
    const [usuario, setUsuario] = useState({
        nome: "",
        perfil: "",
        logado: false,
    });

    useEffect(() => {
        const token = localStorage.getItem("token");
        if (token) {
            try {
                const tokenData = jwtDecode(token); // Decodifica o token
                const tokenExpiracao = tokenData.exp * 1000; // Converte a expirao para ms
                const tempoAtual = Date.now();

                if (tokenExpiracao > tempoAtual) {
                    setUsuario({
                        nome: tokenData.nome,
                        perfil: tokenData.perfil,
                        logado: true,
                    });
                } else {
                    localStorage.removeItem("token");
                }
            } catch (error) {
                console.error("Erro ao decodificar o token:", error);
                localStorage.removeItem("token");
            }
        }
        setProcessando(false);
    }, []);

    return (
        <div className="App">
            {!processando && (
                <ContextoUsuario.Provider value={{ usuario, setUsuario }}>
                {usuario.logado ? (
                    <BrowserRouter>
                        <Routes>
                            <Route path="/LP2-Sistema2" element={<TelaMenu />} />
                            <Route path="/LP2-Sistema2/produto" element={<TelaCadastroProduto />} />
                            <Route path="/LP2-Sistema2/cliente" element={<TelaCadastroCliente />} />
                            <Route path="/LP2-Sistema2/fornecedor" element={<TelaCadastroFornecedor />} />
                            <Route path="/LP2-Sistema2/usuario" element={<TelaCadastroUsuario />} />
                            <Route path="/LP2-Sistema2/categoria" element={<TelaCadastroCategoria />} />
                            <Route path="*" element={<Tela404 />} />
                        </Routes>
                    </BrowserRouter>
                ) : (
                    <TelaLogin />
                )}
            </ContextoUsuario.Provider>
                )} 
        </div>
    );
}

export default App;
